import { Box, Grid, Pagination, Stack, Typography } from "@mui/material";
import React, { useEffect, useRef, useState } from "react";
import { PostsCard } from "./PostsCard";
import axios from "axios";

const Posts = () => {
  const myRef = useRef(null);
  const [blog, setBlog] = useState([]);
  const [post, setPost] = useState([]);
  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_API_URL}/api/blogs/`
        );
        setBlog(res.data);
      } catch (error) {
        console.log(error);
      }
    };
    fetchData(); //goi sau moi lan mo ung ung
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_API_URL}/api/PopularPostApiView/`
        );
        setPost(res.data);
      } catch (error) {
        console.log(error);
      }
    };
    fetchData();
  }, []);

  return (
    <Box>
      <Typography variant="h4" align="center">
        {" "}
        Lastest Recipes{" "}
      </Typography>
      <Grid
        container
        columnSpacing={{ xs: 0, sm: 1, md: 1 }}
        direction={"column"}
      >
        {blog.map((post) => (
          <Grid item xs>
            <PostsCard
              title={post.title}
              excerpt={post.excerpt}
              image={post.image}
              myDirection={"flex"}
            />
          </Grid>
        ))}
      </Grid>
      <Typography
        variant="h4"
        bgcolor={"black"}
        color={"white"}
        align={"center"}
        mt={4}
        md={4}
      >
        Most Popular
      </Typography>
      {post.map((popular) => (
        <Grid item md={6} xs={6}>
        <PostsCard
          title={popular.title}
          excerpt={popular.excerpt}
          image={ `${process.env.REACT_APP_API_URL}${popular.image}`}
          myDirection={"flex"}
          blogHref={`/details/${post.slug}`}
        />
      </Grid>
      ))}
  

      {/* pagination bar  */}
      <Stack
        spacing={2}
        justifyContent={"center"}
        alignItems={"center"}
        mt={3}
        mb={3}
      >
        <Pagination count={10} color="success"></Pagination>
      </Stack>
    </Box>
  );
};

export default Posts;
