import { Box, Typography, styled } from "@mui/material";
import { Container, Stack } from "@mui/system";
import React from "react";

const Footer = () => {
  const MenuBox = styled(Box)({
    display: "flex",
    gap: 30,
    cursor: "pointer",
  });
  const MenuItems = [
    { Name: "Home", Link: "#" },
    { Name: "Recipes", Link: "#" },
    { Name: "About Us", Link: "#" },
    { Name: "Subcrise", Link: "#" },
  ];
  return (
    <Box sx={{ height: "200px", bgcolor: "tomato" }}>
      <Container>
        <Stack direction={"row"}>
          <MenuBox flex={2} sx={{ display: { xs: "none", md: "flex" } }}>
            {MenuItems.map((item) => (
              <Typography variant="body2" color="white">{item.Name}</Typography>
            ))}
          </MenuBox>
          <Box flex={1} color={"white"} mt={1}>
            @2024 Codingstrade, Lern Coding With Than Tam
          </Box>
        </Stack>
        <Typography variant="h4" align="center" mt={8} color={"white"}>Never Stop Learning</Typography>
      </Container>
    </Box>
  );
};

export default Footer;
